header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ===== CTA ===== */

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}



/* ===== Header Socials ===== */

.header__socials {
  margin-top: 1.25rem;
  justify-content: center;
  gap: 1.8rem;
  display: flex;
}

