/* ===== Image ===== */

.Me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 3rem 1.5rem 1.5rem 1.5rem;
}

/* ===== Scroll Down ===== */

.scroll__down{
  position: absolute;
  left: -2.3rem;
  bottom: 5rem;
  transform: rotate(270deg);
  font-weight: 300;
  font-size: 0.9rem;
}


/* ===== Media Queries (Medium Devices) ===== */

@media screen and (max-width: 1024px) {
 header {
    height: 68vh;
  }
}


/* ===== Media Queries (Small Devices) ===== */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .scroll__down, .header__socials{
    display: none;
  }
}